import { prisma } from "@/lib/prisma";

export default async function FlippinPage() {
  const items = await prisma.inventoryItem.findMany({
    orderBy: { createdAt: "desc" },
    take: 50,
  });

  return (
    <section className="space-y-4">
      <div className="flex items-center justify-between">
        <h2 className="text-sm font-semibold tracking-[0.22em] text-emerald-100">
          INVENTORY
        </h2>
        <button className="rounded-md border border-emerald-400/60 px-3 py-1 text-[11px] font-semibold tracking-[0.18em] text-emerald-100 hover:bg-emerald-500/10">
          + INTAKE ITEM
        </button>
      </div>

      <div className="overflow-hidden rounded-md border border-emerald-500/25 bg-black/40">
        <table className="min-w-full text-left text-[11px]">
          <thead className="bg-emerald-500/10 text-emerald-100/80">
            <tr>
              <th className="px-3 py-2 font-semibold tracking-[0.16em]">ADDED</th>
              <th className="px-3 py-2 font-semibold tracking-[0.16em]">TITLE</th>
              <th className="px-3 py-2 font-semibold tracking-[0.16em]">STATUS</th>
              <th className="px-3 py-2 font-semibold tracking-[0.16em]">COST</th>
              <th className="px-3 py-2 font-semibold tracking-[0.16em]">EST VALUE</th>
            </tr>
          </thead>
          <tbody>
            {items.length === 0 ? (
              <tr>
                <td className="px-3 py-4 text-emerald-100/60" colSpan={5}>
                  No items yet. Use â€œINTAKE ITEMâ€ to add your first deal.
                </td>
              </tr>
            ) : (
              items.map((item) => (
                <tr key={item.id} className="border-t border-emerald-500/15 text-emerald-100/90">
                  <td className="px-3 py-2 text-emerald-100/70">
                    {new Date(item.createdAt).toLocaleDateString()}
                  </td>
                  <td className="px-3 py-2">{item.title}</td>
                  <td className="px-3 py-2 text-emerald-100/70">{item.status}</td>
                  <td className="px-3 py-2">
                    ${(item.costCents / 100).toFixed(2)}
                  </td>
                  <td className="px-3 py-2">
                    ${(item.estValueCents / 100).toFixed(2)}
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>
    </section>
  );
}

